.Sectionrecentr {
    max-width: 1280px;
    width: 100%;
    height: 100%;
    margin: 0 auto;
    padding-bottom: 100px;
}

.RecentrLogoH1 {
    display: flex;
    justify-content: center;
    padding-bottom: 50px;
}

.RecentrLogoH1 h1 {
    font-size: 45px;
    font-family: monospace;
}

.DivCintainerRecent {
    display: flex;
    justify-content: space-between;
}
.DivMinContainer img{
    max-width: 370px;
    width: 100%;
    max-height: 250px;
    height: 100%;
}

.DivMinContainer h2 {
    display: flex;
    justify-content: space-between;
    font-family: sans-serif;
    color: grey;
    font-size: 18px;
    font-weight: 400;
}

.DivMinContainer h3 {
    font-family: serif;
    margin: 0
}

.TimeField1 {
    position: relative;
    left: -5px;
    top: 4px;
}

.TimeField2 {
    position: relative;
    left: -4px;
    top: 4px;
}

.TimeField3 {
    position: relative;
    left: 2px;
    top: 4px;
}

.DivMinContainer span {
    color: #25bde4;
    font-weight: 400;
    font-size: 18px;
}

.DivMinContainer p {
    font-family: monospace;
    color: gray;
}

.DivMinContainer {
    margin: 20px;
}

@media(max-width: 1280px) {
    .DivCintainerRecent {
        flex-wrap: wrap;
        justify-content: center;
    }
}

@media(max-width: 860px) {
    .DivMinContainer {
        max-width: 280px;
        width: 100%;
    }

    .DivMinContainer img {
        max-width: 280px;
        width: 100%;
        max-height: 200px;
        height: 100%;
    }

    .DivMinContainer p {
        font-size: 13px;
    }
}
@media(max-width: 580px){
    .RecentrLogoH1 h1 {
        font-size: 35px;
    }
}
@media(max-width: 470px){
    .RecentrLogoH1 h1 {
        font-size: 30px;
    }
}

@media(max-width: 400px){
    .RecentrLogoH1 h1 {
        font-size: 25px;
    }
}